<!-->用户注册修改密码页面<-->
<template>
  <div id="personalSubscription">
    <!-- 标题-->
    <div class="title">订阅标签</div>
    <!-- 标签订阅 -->

    <el-row :gutter="20">
      <el-col :span="6" v-for="(item,index) in tabs" :key="index">
        <el-tooltip :content="'订阅' + item.name + '相关资讯'" placement="right-start">
          <el-switch @change='changeData()' :active-text="item.name" v-model="tabsModel[item.categoryId]" active-value=true inactive-value=false active-color="#13ce66" inactive-color="#ff4949">
          </el-switch>
        </el-tooltip>
      </el-col>
      <el-col>
        <el-button type="primary" round @click="updateSubscription">保存</el-button>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import { getSubscribeList, addSubscribes, getSubscribe } from "@/api/user";
export default {
  name: "personalSubscription",
  data() {
    return {
      tabsModel: {}
    };
  },
  computed: {
    tabs() {
      let newTabs = [];
      global.tabs.forEach(item => {
        if (Reflect.get(item, "categoryId")) {
          let { name, categoryId } = item;
          newTabs[newTabs.length] = { name, categoryId };
        }
      });
      return newTabs;
    }
  },
  methods: {
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "个人中心"
      );
      global.currentPath.data = [index, 2];
    },
    changeData() {
      console.log(arguments);
      console.dir(this.tabsModel);
    },
    updateSubscription() {
      let ids = [];
      for (let i in Object.keys(this.tabsModel)) {
        if (this.tabsModel[i] == "true" && i != undefined) {
          ids.push(Number(i));
        }
      }
      addSubscribes(ids)
        .then(data => {
          if (data.result == "success") {
            this.$message.success({
              showClose: true,
              message: "订阅成功",
              duration: 2000
            });
            getSubscribe(this.HOST)
              .then(data => {
                if (data.message) global.tabs[i].message = data.message;
              })
              .catch(err => {
                new Error(err);
              });
          } else if (data.result == "error") {
            this.$message.error({
              showClose: true,
              message: data.message,
              duration: 2000
            });
          }
        })
        .catch(err => {
          this.$message.error({
            showClose: true,
            message: err,
            duration: 2000
          });
        });
    }
  },
  activated() {
    this.init();
  },
  created() {
    //获取用户已订阅标签
    getSubscribeList().then(data => {
      data.message.forEach((item, index) => {
        if (item.category_id) {
          this.$set(
            this.tabsModel,
            this.tabsModel[item.category_id],
            (this.tabsModel[item.category_id] = "true")
          );
        }
      });
    });
  }
};
</script>


<style lang="scss" scoped>
#personalSubscription {
  height: 100%;
  width: 1200px;
  padding: 50px;
  margin: 10px auto;
  background: rgba(235, 240, 247, 0.932);
  border-radius: 2px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;

  .title {
    margin: 10px 0 0 10px;
    padding: 10px 5px 0 5px;
    color: #576ea1;
    font-size: 22px;
    font-weight: 600;
  }
}
.bg-purple {
  background: #d3dce6;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.el-col {
  margin-bottom: 30px;
  &:last-child {
    margin-bottom: 0;
    text-align: center;
    .el-button {
      margin: 10px auto;
    }
  }
}
.el-row {
  padding: 40px 30px;
}
</style>



